import { useContext, useEffect, useRef, useState } from 'react'
import { Link, Outlet } from 'react-router-dom'
import { animateScroll as scroll } from 'react-scroll'

import './HeaderStyles.css'

export function Header() {
  const menuRef = useRef(null)
  const [OpenMenu, SetOpenMenu] = useState(false)
  const [isHeaderVisible, setIsHeaderVisible] = useState(false)
  const handleToggleMenu = () => {
    SetOpenMenu(!OpenMenu)
  }

  // адаптивное меню
  useEffect(() => {
    const handleResize = () => {
      if (menuRef.current && menuRef.current.offsetWidth < 769) {
        SetOpenMenu(false)
      } else {
        SetOpenMenu(true)
      }
    }
    window.addEventListener('resize', handleResize)
    handleResize()
    return () => window.removeEventListener('resize', handleResize)
  }, [])
  // плавный скролл
  useEffect(() => {
    const handleScroll = () => {
      setIsHeaderVisible(window.scrollY > 0)
    }
    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  return (
    <header className='Header' ref={menuRef}>
      <article className={`Header__article ${OpenMenu === true ? 'active' : ''}`}>
        <Link className='Header__article__link' onClick={() => scroll.scrollTo()}>
          <h3 className='Header__article__link__text'>Обо мне</h3>
        </Link>
        <Link className='Header__article__link' onClick={() => scroll.scrollToTop()}>
          <h3 className='Header__article__link__text'>Главная</h3>
        </Link>
        <Link className='Header__article__link' onClick={() => scroll.scrollTo()}>
          <h3 className='Header__article__link__text'>Скиллы</h3>
        </Link>
        <Link className='Header__article__link' onClick={() => scroll.scrollTo()}>
          <h3 className='Header__article__link__text'>Проекты</h3>
        </Link>
        <Link className='Header__article__link' to={'/donat'} target='_blank'>
          <h3 className='Header__article__link__text donates'>Поддержка автора</h3>
        </Link>
      </article>
      <article className='Header__article1' onClick={() => handleToggleMenu()}>
        <button className='Header__article__button--open-menu'>
          <span className={`${OpenMenu === true ? 'spanes' : 'span1'}`}>{OpenMenu === true ? 'х' : '='}</span> Меню
        </button>
      </article>
      <Outlet />
    </header>
  )
}
